'use strict';
const angular = require('angular');

export class roomGuestPickerComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'World';
  }
  rooms = 1;
  guests = 2;

  plusRoom() {
    this.rooms++;
    if (this.guests < this.rooms) {
      this.guests = this.rooms;
    }
  }
  minusRoom() {
    this.rooms--;
    if (this.rooms < 1) {
      this.rooms = 1;
    }
    var maxNumberOfPeopleWeCanHave = this.rooms * 4;
    if (maxNumberOfPeopleWeCanHave < this.guests) {
      this.guests = maxNumberOfPeopleWeCanHave;
    }
  }
  plusGuests() {
    this.guests++;
    if (this.guests / this.rooms > 4) {
      this.rooms++;
    }
  }
  minusGuests() {
    this.guests--;
    if (this.guests < 1) {
      this.guests = 1;
    } else if (this.guests < this.rooms) {
      this.rooms = this.guests;
    }
  }
}

export default angular.module('eventixFullstackApp.roomGuestPicker', [])
  .component('roomGuestPicker', {
    template: `  <md-menu md-offset="0 60">
        <div class="height-100 pt2 pl2" ng-click="$mdOpenMenu($event)">
          <span class="height-100 md-subhead">{{$ctrl.rooms}} Room {{$ctrl.guests}} Guests</span>
          <i class="fa fa-chevron-down grayText right pr2" style="padding-top:5px;"> </i>
        </div>
        <md-menu-content width="4">
          <md-menu-item class="pt1" >
            <div layout="row">
              <span flex="40" class="pt1 pl1 md-subhead">Rooms</span>
              <button class=" small_button button icon h6 fa fa-minus  "  ng-click="$ctrl.minusRoom()" md-prevent-menu-close="md-prevent-menu-close"></button>
              <span flex="20" class="pt1 md-title center">{{$ctrl.rooms}}</span>
              <button class=" small_button button icon h6 fa fa-plus "  ng-click="$ctrl.plusRoom()" md-prevent-menu-close="md-prevent-menu-close"></button>
            </div>
          </md-menu-item>
          <md-menu-item class="pt1" >
            <div layout="row">
              <span flex="40" class="pt1 pl1 md-subhead">Guests</span>
              <button class=" small_button button fa fa-minus"  ng-click="$ctrl.minusGuests()" md-prevent-menu-close="md-prevent-menu-close"></button>
              <span flex="20" class="pt1 md-title center">{{$ctrl.guests}}</span>
              <button class=" small_button button fa fa-plus"  ng-click="$ctrl.plusGuests()" md-prevent-menu-close="md-prevent-menu-close"></button>
            </div>
          </md-menu-item>
        </md-menu-content>
      </md-menu>`,
    bindings: {
      rooms: '=',
      guests: '='
    },
    controller: roomGuestPickerComponent
  })
  .name;



