'use strict';

export default class LoginController {
  user = {
    name: '',
    email: '',
    password: ''
  };
  errors = {
    login: undefined
  };
  submitted = false;


  /*@ngInject*/
  constructor(Auth, $state, $mdDialog) {
    this.Auth = Auth;
    this.$state = $state;
    this.$mdDialog = $mdDialog;
  }

  closeDialog() {
    this.$mdDialog.hide();
  }

  showSignup(ev) {
    this.closeDialog();
    this.$mdDialog.show({
        template: '<signup></signup>',
        parent: angular.element(document.body),
        targetEvent: ev,
        clickOutsideToClose: true
      })
      .then(function (answer) {
        console.log(answer);
      }, function () {
        console.log('You cancelled the dialog.');
      });
  }

  login(form) {
    this.submitted = true;

    if (form.$valid) {
      this.Auth.login({
          email: this.user.email,
          password: this.user.password
        })
        .then(() => {
          this.closeDialog();
        })
        .catch(err => {
          this.errors.login = err.message;
        });
    }
  }
}



