export default locals => ({
  locals, // will be bound to the controller instance!
  template: `
<md-dialog-content>
  <md-dialog-content layout="column">
  <md-toolbar layout="row" class="md-warn">
     <div class="md-toolbar-tools" style="margin-right: 2px;">
       <span>Need some more Info before we can get started </span>
     </div>
   </md-toolbar>
   <div class="px3 py3" >
  <div class="my1" layout="row" layout-align="space-between center">
    <md-subheader class="bg-white">Event Check in date </md-subheader>
    <md-datepicker flex="50"
    md-is-open="$ctrl.openDatePicker2"
    ng-click="$ctrl.openDatePicker2 = true"
    required md-input-name="checkInDate"
    class="md-no-underline margin-right md-warn "
    ng-model="$ctrl.eventDetails.checkin"
    md-placeholder="Check In"></md-datepicker>
  </div>
    <div class="my1" layout="row" layout-align="space-between center">
      <md-subheader class="bg-white">Event Check Out date </md-subheader>
      <md-datepicker
        md-is-open="$ctrl.openDatePicker1"
        ng-click="$ctrl.openDatePicker1 = true"
        required
        md-input-name="checkOutDate"
        class="md-no-underline margin-right md-warn "
        flex="50"
        ng-model="$ctrl.eventDetails.checkout"
        md-placeholder="Check out"></md-datepicker>
    </div>
    <div class="ml2 mr3">
      <room-guest-picker rooms="$ctrl.rooms" guests="$ctrl.guests"></room-guest-picker>
      </div>

    </div>
  </md-dialog-content>
  <md-dialog-actions>
    <md-button ng-click="$ctrl.cancel()">Close</md-button>
    <md-button class="md-warn md-raised" ng-click="$ctrl.onSave($ctrl.eventDetails)">Next</md-button>
  </md-dialog-actions>
 </md-dialog-content>
`,
  bindToController: true,
  controllerAs: '$ctrl',
  controller: Controller,
  clickOutsideToClose: true,
  fullscreen: true
});
Controller.$inject = ['$mdDialog'];

function Controller($mdDialog) {
  // this.foo = ..will be provided in locals as an input parameter..
  // this.onSave = () { ..will be provided as output parameter.. }
  this.cancel = () => {
    $mdDialog.cancel();
  };
}



