import angular from 'angular';
import uiRouter from 'angular-ui-router';
import routing from './main.routes';
import moment from 'moment';

export class MainController {
  selectedEvent = {
    numOfAttendees: '1 Room, 2 Attendees',
    servicesType: '3'
  };
  oldSelectedEvent = {};
  recomendedEvents = {};

  /*@ngInject*/
  constructor($scope, socket, $state, EventSearch) {
    this.socket = socket;
    this.$state = $state;
    this.EventSearch = EventSearch;

    $scope.$on('$destroy', function () {
      socket.unsyncUpdates('thing');
    });
  }


  $onInit() {
    this.EventSearch.recomendedEvents().then(data => {
      console.log(data);
      this.recomendedEvents = data;
    });
  }

  $doCheck() {
    if (this.selectedEvent.eventSearch) {
      var current = this.selectedEvent.eventSearch;
      if (current.id !== this.oldSelectedEvent.id) {
        this.selectedEvent.checkin = this.subtractkDays(current.dates.start.localDate, 1);
        this.selectedEvent.checkout = current.dates.end ? this.addDays(current.dates.start.localDate, 2) : this.addDays(current.dates.start.localDate, 2);
        this.oldSelectedEvent = current;
      }
      if (current.showAllIndex) {
        console.log(current, 'cur');
        console.log(this.selectedEvent.selectedArray, 'passssssss');
        this.$state.go('eventList', {
          eventsVenuesArr: this.selectedEvent.selectedArray
        });
      }
    }
  }

  subtractkDays(date, num) {
    if (moment().format('YYYY-MM-DD') !== date) {
      return moment(date, 'YYYY-MM-DD').subtract(num, 'days')
        .toDate();
    } else {
      return moment(date, 'YYYY-MM-DD').toDate();
    }
  }

  addDays(date, num) {
    return moment(date, 'YYYY-MM-DD').add(num, 'days')
      .toDate();
  }

  searchForFlights(selectedEvent) {
    this.$state.go('eventInfo', {
      events: selectedEvent
    });
  }

}

export default angular.module('eventixFullstackApp.main', [uiRouter])
  .config(routing)
  .component('main', {
    template: require('./main.html'),
    controller: MainController
  })
  .name;



