'use strict';
const angular = require('angular');

export default angular.module('eventixFullstackApp.eventSearchTypeahead', [])
  .directive('eventSearchTypeahead', EventSearchTypeahead)
  .controller('EventSearchTypeaheadCtrl', EventSearchTypeaheadCtrl)
  .name;

function EventSearchTypeahead() {
  return {
    template: require('./eventSearchTypeahead.html'),
    restrict: 'EA',
    replace: true,
    scope: {
      selectedEvent: '=',
      selectedArr: '='
    },
    controller: 'EventSearchTypeaheadCtrl',
    controllerAs: 'ctrl',
    bindToController: true
  };
}

function EventSearchTypeaheadCtrl($timeout, $q, $log, EventSearch, FlightSearch) {
  var self = this;
  self.simulateQuery = true;
  self.isDisabled = false;

  self.events = loadAll();
  self.querySearch = querySearch;
  self.selectedItemChange = selectedItemChange;
  self.searchTextChange = searchTextChange;
  self.showhidden = showhidden;

  // ******************************
  // Internal methods
  // ******************************

  function showhidden(e) {
    var autoChild = document.getElementsByClassName('md-autocomplete-suggestions-container').firstElementChild;
    var el = angular.element('.md-autocomplete-suggestions-container');
    console.log(el.scope().$mdAutocompleteCtrl.hidden)
    el.scope().$mdAutocompleteCtrl.hidden = false;
  }

  /**
   * Search for events... use $timeout to simulate
   * remote dataservice call.
   */
  function querySearch(query) {
    var results = query ? self.events.filter(createFilterFor(query)) : EventSearch.events,
      deferred;
    if (self.simulateQuery && results.length === 0) {
      deferred = $q.defer();
      EventSearch.searchUSEventsWithKey(query).then(function (res) {
        $log.log(res);
        self.selectedArr = res;
        if (res.length > 0) {
          self.events = res;
          addValue(self.events);
          deferred.resolve(res);
        } else {
          return [];
        }
      });
      return deferred.promise;
    } else {
      self.selectedArr = results;
      return results;
    }
  }

  function searchTextChange(text) {
    $log.info('Text changed to ' + text);
  }

  function selectedItemChange(item) {
    console.log(item);
    if (item.type === 'event') {
      EventSearch.getEventDetailById(item.id).then(function (evt) {
        if (!evt._embedded.venues[0].location) {
          FlightSearch.geocode(evt._embedded.venues['0'].address.line1 + ' ' + evt._embedded.venues['0'].city.name).then(function (loc) {
            evt._embedded.venues[0].location = {
              latitude: loc.results['0'].geometry.location.lat,
              longitude: loc.results['0'].geometry.location.lng
            };
            self.selectedEvent = evt;
          });
        } else {
          self.selectedEvent = evt;
        }
      });
    } else {
      self.selectedEvent = item;
    }
  }

  /**
   * Build `components` list of key/value pairs
   */
  function loadAll() {
    var events = EventSearch.events;
    return addValue(events);
  }

  function addValue(events) {
    return events.map(function (ev) {
      ev.value = ev.name.toLowerCase();
      return ev;
    });
  }

  /**
   * Create filter function for a query string
   */
  function createFilterFor(query) {
    var lowercaseQuery = angular.lowercase(query);

    return function filterFn(item) {
      return (item.value.indexOf(lowercaseQuery) === 0);
    };

  }
}


