'use strict';
const angular = require('angular');

export class flightDealsComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'World';
  }

  legsLookup(id) {
    var property = '';
    this.flights.legs.forEach(function(leg) {
      if(leg.legId === id) {
        property = leg.segments;
      }
    });
    return property;
  }
}

export default angular.module('eventixFullstackApp.flightDeals', [])
  .component('flightDeals', {
    template: `<div class="padding center">
      <div layout="row" class="center">
        <div flex="80">
          <div layout="row">
            <div flex="20">
              <p class="pl2">
                From -> to
              </p>
            </div>
            <p flex="20">
              airline Name
            </p>
            <p flex="10">
              flight Number
            </p>
            <p flex="25" class="center">
              Departure Time
            </p>
            <p flex="25" class="center">
              Arrival Time
            </p>
          </div>
        </div>
        <div flex="20" layout="row">
          <p flex="50">
            # Of Tickets
          </p>
          <p flex="50">
            Flight Price
          </p>
        </div>
      </div>

      <md-card ng-repeat="flight in $ctrl.flights track by $index" class="padding">
        <div layout="row">
          <div flex="80">
            <div layout="row" ng-repeat="segment in flight.segments">
              <div flex="20">
                <p>
                  {{segment.departureAirportCode}} -> {{segment.arrivalAirportCode}}
                </p>
              </div>
              <p flex="20">
                {{segment.airlineName}}
              </p>
              <p flex="10">
                {{segment.flightNumber}}
              </p>
              <p flex="25">
                {{segment.departureTime | date}}
              </p>
              <p flex="25">
                {{segment.arrivalTime | date}}
              </p>
            </div>
          </div>
          <div flex="20" layout="row">
            <p flex="50">
              {{flight.numberOfTickets}}
            </p>
            <p flex="50">
              {{flight.totalPrice.formattedWholePrice}}
            </p>
          </div>
        </div>
      </md-card>
    </div>`,
    bindings: { flights: '=' },
    controller: flightDealsComponent
  })
  .name;



