import angular from 'angular';
import uiRouter from 'angular-ui-router';
import routing from './eventList.routes';
import moment from 'moment';
import myDialog from './dialog';


export class EventListComponent {
  /*@ngInject*/
  constructor($stateParams, $mdDialog, $state) {
    this.$stateParams = $stateParams;
    this.$state = $state;
    this.$mdDialog = $mdDialog;
    this.message = 'Hello';
  }
  $onInit() {
    this.eventsVenues = this.$stateParams.eventsVenuesArr;
    this.backgroundImgUrl = _.maxBy(this.eventsVenues[1].images, 'width');
    this.overViewImgUrl = _.filter(this.eventsVenues[1].images, {
      ratio: '3_2'
    });
  }
  formatTime(time) {
    return moment(time, 'HH:mm:ss').format('h:mm A');
  }
  selectedEvent(item) {
    console.log(item);
  }
  showOptionsDialog(ev) {
    this.$mdDialog.show(myDialog({
      eventDetails: {
        ev,
        checkin: this.subtractkDays(ev.dates.start.localDate, 1),
        checkout: ev.dates.end ? this.addDays(ev.dates.start.localDate, 2) : this.addDays(ev.dates.start.localDate, 2)
      },
      onSave: dat => {
        const selectedEvent = {
          eventSearch: ev,
          checkIn: dat.checkin,
          checkout: dat.checkout,
          departureAirport: dat.departureAirport,
          servicesType: '3',
          selectedArray: this.eventsVenues,
          numOfAttendees: '1 Room, 2 Attendees'
        };
        console.log(selectedEvent);

        this.$mdDialog.hide();
        this.$state.go('eventInfo', {
          events: selectedEvent
        });
      }
    }));
  }


  subtractkDays(date, num) {
    if (moment().format('YYYY-MM-DD') !== date) {
      return moment(date, 'YYYY-MM-DD').subtract(num, 'days')
        .toDate();
    } else {
      return moment(date, 'YYYY-MM-DD').toDate();
    }
  }

  addDays(date, num) {
    return moment(date, 'YYYY-MM-DD').add(num, 'days')
      .toDate();
  }


}

export default angular.module('eventixFullstackApp.eventList', [uiRouter])
  .config(routing)
  .component('eventList', {
    template: require('./eventList.html'),
    controller: EventListComponent
  })
  .name;



