import angular from 'angular';
import uiRouter from 'angular-ui-router';
import routing from './eventInfo.routes';
import moment from 'moment';

export class EventInfoController {
  backgroundImgUrl;
  overViewImgUrl;
  events = {};
  flights = {};
  oldSelectedEvent = {};

  eventOffers = {};
  packages = {};
  packageSteps = 1;

  hoveredOnPackage = {};
  selected;
  selectedPkgType = 'gold'
  packagesInfo = [
    'Our lowest cost option which includes event tickets, hotel accommodations, and flights',
    'Our most popular option which includes event tickets, hotel accommodations, and flights',
    'Our most exclusive option which includes event tickets, hotel accommodations, and flights',
    'Our most expensive option which includes event tickets, hotel accommodations, and flights',
  ];

  /*@ngInject*/
  constructor($http, $stateParams, $mdDialog, FlightSearch, EventSearch, HotelSearch) {
    this.$http = $http;
    this.$mdDialog = $mdDialog;
    this.$stateParams = $stateParams;
    this.FlightSearch = FlightSearch;
    this.EventSearch = EventSearch;
    this.HotelSearch = HotelSearch;
  }

  /*On init lifecycle hook*/
  $onInit() {
    this.events = this.$stateParams.events;
    console.log(this.events);
    if (!this.events) {
      ///api/packages/0?lat=33.445899&long=-112.071313&departureDate=2016-11-20T00:00:00-05:00&arrivalDate=2016-11-23T00:00:00-05:00&departureAirport=JFK&eventId=G5v0ZKzRfNKGP&adults=2&childrens=0
      this.$http.get('/api/things').then(res => {
        this.packages = res.data.data;
        console.log(this.events);
      });
    } else {
      this.parseEvent();
    }
  }

  // changePackageType(pkg) {
  //   if (_.isEmpty(this.packages)) return;
  //   this.selectedPkgType = pkg.toLowerCase();
  //   this.hotelOffers = this.packages[this.selectedPkgType].hotel;
  //   this.eventOffers = this.packages[this.selectedPkgType].events;
  //   this.flights = this.packages[this.selectedPkgType].flight;
  // }

  packageHoveredOn(pkg, pkgName) {
    this.hoveredOnPackage.name = pkgName;
    this.hoveredOnPackage.minEvent = _.minBy(pkg.value.events.formatedOffers, function (o) {
      if (o.prices) {
        return Number(o.prices.total);
      }
    });
    this.hoveredOnPackage.minHotel = _.minBy(pkg.value.hotel, function (o) {
      return Number(o.lowRate);
    });
  }

  selectedPackage(pkg, pkgName) {
    this.selected = {
      name: pkgName,
      minEventRateSelected: pkg.minEventRateSelected,
      minHotelRateSelected: pkg.minEventRateSelected,
      value: pkg.value
    };
    this.selected.value.events.formatedOffers = this.selected.value.events.formatedOffers.reverse();
  }

  selectedTicket(ticket) {
    this.selected.ticket = ticket;
  }

  selectedHotel(hotel) {
    this.selected.hotel = hotel;
  }


  calculatePriceDifference(amount, pkg) {
      if (amount) {
        let dif = Number(amount) - pkg.minEventRateSelected;
        if (typeof dif === 'number' && !isNaN(dif)) {
          return dif;
        }
      }
    }
    // isPkgSelected(pkg) {
    //   return pkg.toLowerCase() === this.selectedPkgType ? 'bg-gray' : 'bg-white';
    // }

  calculatePackagesStartingPrice(pkg, pkgName) {
    const minEvent = _.minBy(pkg.value.events.formatedOffers, function (o) {
      if (o.prices) {
        return Number(o.prices.total);
      }
    });
    const minHotel = _.minBy(pkg.value.hotel, function (o) {
      return Number(o.lowRate);
    });
    pkg.minHotelRateSelected = minHotel.lowRate;
    pkg.minEventRateSelected = Number(minEvent.prices.total);
    console.log(`${pkgName}: Hotel Price ${minHotel.lowRate}, Event Price ${Number(minEvent.prices.total)}`);
    return minHotel.lowRate + Number(minEvent.prices.total);
  }

  parseEvent() {
    // this.backgroundImgUrl = _.maxBy(this.events.eventSearch.images, 'width');
    // this.overViewImgUrl = _.filter(this.events.eventSearch.images, {
    //   ratio: '3_2'
    // });
    this.events.checkin = moment(this.events.checkin).format();
    this.events.checkout = moment(this.events.checkout).format();
    // this.checkForFlights();
    // this.checkforEvents();
    // this.checkforHotels();
    this.getPackage(0);
    this.events.checkin = moment(this.events.checkin).toDate();
    this.events.checkout = moment(this.events.checkout).toDate();
  }

  // checkForFlights() {
  //   var geoLocation = this.events.eventSearch._embedded.venues[0].location;
  //   this.FlightSearch.searchForFlights(this.events.departureAirport.name, geoLocation.latitude, geoLocation.longitude, this.events.checkin)
  //     .then(res => {
  //       this.flights = res;
  //     });
  // }

  // checkforEvents() {
  //   this.EventSearch.getEventOffers(this.events.eventSearch.id)
  //     .then(data => {
  //       this.eventOffers = data;
  //       console.log(data);
  //     });
  // }

  // checkforHotels() {
  //   var geoLocation = this.events.eventSearch._embedded.venues[0].location;
  //   this.HotelSearch.getListOfHotels(`${geoLocation.latitude},${geoLocation.longitude}`, this.formatDate(this.events.checkin), this.formatDate(this.events.checkout))
  //     .then(dat => {
  //       this.hotelOffers = dat;
  //       console.log(dat);
  //     });
  // }
  getPackage(packageId) {
    let departureAirport = this.events.departureAirport ? this.events.departureAirport.name : 'JFK';
    const geoLocation = this.events.eventSearch._embedded.venues[0].location;
    const venue = this.events.eventSearch._embedded.venues[0];
    // /api/packages/0?lat=33.445899&long=-112.071313&departureDate=2016-11-20T00:00:00-05:00&arrivalDate=2016-11-23T00:00:00-05:00&departureAirport=JFK&eventId=G5v0ZKzRfNKGP&adults=2&childrens=0
    const url = `/api/packages/${packageId}?lat=${geoLocation.latitude}&long=${geoLocation.longitude}&departureDate=${this.events.checkin}&arrivalDate=${this.events.checkout}&departureAirport=${departureAirport}&eventId=${this.events.eventSearch.id}&adults=${this.events.guests}&childrens=0&eventCity=${venue.city.name}&eventCountry=${venue.country.name}`;
    this.$http.get(url).then(dat => {
        if (!dat.data['Basic Package'].value.hotels) {
          this.$mdDialog.show(
            this.$mdDialog.alert()
            .parent(angular.element(document.querySelector('#popupContainer')))
            .clickOutsideToClose(true)
            .title('Error occured while packaging hotels!')
            .textContent('Looks like we couldn\'t find any hotel deals for this event')
            .ariaLabel('Error occured while packaging hotels!')
            .ok('Got it!')
          );
        }
        this.packages = dat.data;
        console.log(dat);
      })
      .catch(err => {
        this.$mdDialog.show(
          this.$mdDialog.alert()
          .parent(angular.element(document.querySelector('#popupContainer')))
          .clickOutsideToClose(true)
          .title('Error occured while packaging hotels!')
          .textContent(JSON.parse(err.data.error).errors[0].detail)
          .ariaLabel('Error occured while packaging hotels!')
          .ok('Got it!')
        );
      });
  }

  getNumber(num) {
    num = Math.floor(Number(num));
    return new Array(num);
  }
  $doCheck() {}

  formatDate(date) {
    return moment(date).format('YYYY-MM-DD');
  }
}



export default angular.module('eventixFullstackApp.eventInfo', [uiRouter])
  .config(routing)
  .component('eventInfo', {
    template: require('./eventInfo.html'),
    controller: EventInfoController
  })
  .name;



