'use strict';
const angular = require('angular');

/*@ngInject*/
export function HotelSearchService($http) {
  // AngularJS will instantiate a singleton by calling "new" on this function
  this.getListOfHotels = (location, checkin, checkout) => {
    return $http.get(`api/hotels?location=${location}&checkInDate=${checkin}&checkOutDate=${checkout}`)
      .then(dat => dat.data);
  };
}

export default angular.module('eventixFullstackApp.HotelSearch', [])
  .service('HotelSearch', HotelSearchService)
  .name;



