'use strict';

import angular from 'angular';
// import ngAnimate from 'angular-animate';
import ngCookies from 'angular-cookies';
import ngResource from 'angular-resource';
import ngSanitize from 'angular-sanitize';
import 'angular-socket-io';
import uiRouter from 'angular-ui-router';
import uiBootstrap from 'angular-ui-bootstrap';
// import ngMessages from 'angular-messages';
// import ngValidationMatch from 'angular-validation-match';


import {
  routeConfig
} from './app.config';

import _Auth from '../components/auth/auth.module';
import account from './account';
import admin from './admin';
import navbar from '../components/navbar/navbar.component';
import footer from '../components/footer/footer.component';
import eventSearchTypeahead from './eventSearchTypeahead/eventSearchTypeahead.directive';
import EventSearch from '../components/EventSearch/EventSearch.service.js';
import FlightSearch from '../components/FlightSearch/FlightSearch.service.js';
import HotelSearch from '../components/HotelSearch/HotelSearch.service.js';
import airportAutocomplete from '../components/airportAutocomplete/airportAutocomplete.component';
import roomGuestsPicker from '../components/roomGuestPicker/roomGuestPicker.component.js';
import main from './main/main.component';
import eventInfo from './eventInfo/eventInfo.component';
import eventList from './eventList/eventList.component';
import flightDeals from '../components/flightDeals/flightDeals.component';
import constants from './app.constants';
import util from '../components/util/util.module';
import socket from '../components/socket/socket.service';

import './app.scss';

angular.module('eventixFullstackApp', ['ui.router', 'ngMaterial', ngCookies, ngResource, ngSanitize, 'btford.socket-io',
    uiRouter, uiBootstrap, _Auth, account, admin, navbar, footer, main, constants, socket, util, eventSearchTypeahead, EventSearch, FlightSearch, eventInfo,
    airportAutocomplete, flightDeals, HotelSearch, eventList, roomGuestsPicker
  ])
  .config(routeConfig)
  .run(function ($rootScope, $location, Auth) {
    'ngInject';
    // Redirect to login if route requires auth and you're not logged in

    $rootScope.$on('$stateChangeStart', function (event, next) {
      Auth.isLoggedIn(function (loggedIn) {
        if (next.authenticate && !loggedIn) {
          $location.path('/login');
        }
      });
    });
  });

angular.element(document)
  .ready(() => {
    angular.bootstrap(document, ['eventixFullstackApp'], {
      strictDi: true
    });
  });



