'use strict';

export default function($stateProvider) {
  'ngInject';

  $stateProvider
    .state('eventInfo', {
      url: '/eventInfo',
      template: '<event-info></event-info>',
       params: {
         events: null
       },
       onEnter: function ($state, $stateParams) {
         if (!$stateParams.events) {
           // $state.go('main');
         }
       }
    });
}



