'use strict';
const angular = require('angular');

/*@ngInject*/
export function EventSearchService($http, $log) {
  // AngularJS will instantiate a singleton by calling "new" on this function
  var Service = this;
  Service.getUSEvents = getUSEvents;
  Service.searchUSEventsWithKey = searchUSEventsWithKey;
	Service.getEventOffers = getEventOffers;
  Service.getEventDetailById = getEventDetailById;
  Service.recomendedEvents = recomendedEvents;
  Service.events = [];
  $log.log('Hello from your Service: EventService in module main');

  return Service;

  function getUSEvents() {
    var eventUrsl = 'http://app.ticketmaster.com/discovery/v2/events.json?countryCode=US&apikey=g4sXxf0ioySFxO0FQFYnGMEoAwW1uMoQ';
    return $http.get(eventUrsl).then(function (data) {
      return data.data;
    });
  }

  function recomendedEvents() {
     return $http.get('/api/events/recomendedEvents/').then(res => res.data);
  }

  function getEventOffers(id) {
    return $http.get(`/api/events/${id}/offers`).then(function (data) {
      return data.data;
    });
  }


  function searchUSEventsWithKey(key) {
    return $http.get(`/api/events?key=${key}`).then(function (data) {
      if (data.data) {
        data.data.unshift({name: 'See all results ...', showAllIndex: true});
        return data.data;
      } else {
        return [];
      }
    });
  }

  function getEventDetailById(id) {
    var eventUrsl = 'http://app.ticketmaster.com/discovery/v2/events/' + id + '.json?apikey=g4sXxf0ioySFxO0FQFYnGMEoAwW1uMoQ';
    return $http.get(eventUrsl).then(function (data) {
      if (data.data._embedded) {
        return data.data;
      } else {
        return [];
      }
    });
  }

}

export default angular.module('eventixFullstackApp.EventSearch', [])
  .service('EventSearch', EventSearchService)
  .name;



