'use strict';

export default function ($stateProvider) {
  'ngInject';

  $stateProvider
    .state('eventList', {
      url: '/eventList',
      template: '<event-list></event-list>',
      params: {
        eventsVenuesArr: null
      },
      onEnter: function ($state, $stateParams) {
        if (!$stateParams.eventsVenuesArr) {
          $state.go('main');
        }
      }
    });
}



