'use strict';
const angular = require('angular');

export class airportAutocompleteComponent {
  /*@ngInject*/
  constructor(FlightSearch) {
    this.FlightSearch = FlightSearch;
  }

  simulateQuery = true;
  isDisabled = false;

  selectedAirport = {};

  // ******************************
  // Internal methods
  // ******************************

  /**
   * Search for events... use $timeout to simulate
   * remote dataservice call.
   */
  $onInit() {
    this.events = this.loadAll();
  }

  querySearch(query) {
    var results = query ? this.events.filter(this.createFilterFor(query)) : this.FlightSearch.airportCode;
    return results;
  }

  searchTextChange() {}

  selectedItemChange(item) {
    this.selectedAirport = item;
  }

  /**
   * Build `components` list of key/value pairs
   */
  loadAll() {
    var airports = this.FlightSearch.airportCode;
    return this.addValue(airports);
  }

  addValue(airports) {
    return airports.map(ev => {
      ev.value = ev.name.toLowerCase();
      return ev;
    });
  }

  /**
   * Create filter for a query string
   */
  createFilterFor(query) {
    var lowercaseQuery = angular.lowercase(query);
    return function filterFn(item) {
      return item.value.indexOf(lowercaseQuery) === 0;
    };
  }

}

export default angular.module('eventixFullstackApp.airportAutocomplete', [])
  .component('airportAutocomplete', {
    template: `<div layout="column" ng-cloak>
      <md-autocomplete ng-disabled="$ctrl.isDisabled" ng-required="$ctrl.required" md-selected-item="$ctrl.selectedItem" md-search-text-change="$ctrl.searchTextChange($ctrl.searchText)"
        md-search-text="$ctrl.searchText" md-selected-item-change="$ctrl.selectedItemChange(item)" md-items="item in $ctrl.querySearch($ctrl.searchText)"
        md-item-text="item.name" placeholder="{{$ctrl.placeholder}}" md-menu-class="autocomplete-custom-template">
        <md-item-template>
          <span class="item-metadata">
                <span class="item-metastat">
            <strong>{{item.name}}</strong>
            </span>
          </span>
        </md-item-template>
      </md-autocomplete>
    </div>`,
    bindings: {
      selectedAirport: '=',
      placeholder: '@',
      required: '='
    },
    controller: airportAutocompleteComponent
  })
  .name;



